/* Reset styles */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
button,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
   margin: 0;
   padding: 0;
   border: none;
   font-family: inherit;
   font-size: inherit;
   vertical-align: baseline;
}

body {
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
   display: block;
}

ol,
ul {
   list-style: none;
}

blockquote,
q {
   quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
   content: '';
   content: none;
}

table {
   border-collapse: collapse;
   border-spacing: 0;
}

* {
   box-sizing: border-box;
   line-height: 1;
}

p {
   line-height: normal;
}

p::first-line {
   line-height: 1;
}

a {
   color: inherit;
   text-decoration: none;
}

a:focus {
   @media (pointer: coarse) {
      outline: none;
   }
}

img {
   display: block;
   max-width: 100%;
}

button {
   padding-inline-start: 0;
   padding-inline-end: 0;
   color: inherit;
   background: none;
   cursor: pointer;
}

button:active,
button:focus {
   outline: none;
}

hr {
   height: 1px;
   border: none;
}

address {
   font-style: normal;
}

button,
a {
   -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
